$main-bg: #DBCDC6;
$app-bg: #EAD7D1;
$block-bg: #7B506F;

.sched-unit {
    background-color: $block-bg;
    color: $main-bg;
    text-align: center;
    min-width: 8em;
    max-width: 15em;
    width: clamp(8em, 10em, 20em);
    border-top-right-radius: 27.5%;
    border-top-left-radius: 27.5%;
    .pop-up {
        display: none; } }

